import './App.css';
import React, { useEffect, useState } from "react";
import Number from './components/number_buttons'
import Symbol from './components/symbol_buttons'
import Entry from "./components/entry"


function App() {
  const [nums, setnums] = useState("");
  const [total, updateTotal] = useState("");
  

  const signs = ["%", "÷", "-", "+", "x", "**", "/", "*","AC","-/+", "=", "."]

  function applyOpperation(value){


      
    if (nums === Infinity || total === Infinity ||
       (nums == "-Infinity" || total == "-Infinity")){
      updateTotal("MATH ERROR");
      setnums("MATH ERROR");
      return;
    }


    if (nums == "MATH ERROR"){
      setnums("");
      updateTotal("");
      return;
    }

    if (nums == "" && value == 0){
      return;
    }

    // convert to readable string
    if(value == "÷"){
      value = "/"
    }else if(value == "x"){ 
      value = "*"
    }


    // compute values
    if(value == "AC"){
      setnums("");
      updateTotal("");
      return;
    }
    
    if(value == "="){
      setnums(total.toString())
      return;
    }

    if(value == "-/+"){
      updateTotal((-1*total).toString())
      setnums((-1*total).toString())
      return;
    }


    if (
      signs.includes(value) && nums === '' ||
      signs.includes(value) && signs.includes(nums.slice(-1))
    ){
      return;
    }

    setnums(nums+ value)

    if(!signs.includes(value)){
      if (nums.slice(-1) == signs.includes(value) && 
      signs.includes(value) && 
      signs.includes(nums.slice(-2))){
        return;
      }
      try{ updateTotal(eval(nums + value.toString()));}
      catch(e){ updateTotal("MATH ERROR"); setnums("MATH ERROR"); return;}
  
    }

    if(value == "%"){
      setnums((parseFloat(total)/100).toString())
      return;
    }


  }



  // rendered below
  return (

    <main className="App-header" >

      <div className="calc_box">
        <Entry display={nums || "0"}/>
        <div className="the_press_zone">

          <Number value={nums} onClick={(value) => {applyOpperation(value)}}/>
          <Symbol onClick={(sign) => {applyOpperation(sign);}}/>

        </div>
      </div>


    </main>

  );
}

export default App;
