import React, { Component } from "react";
import Entry from "./entry"



class Number extends Component{
    state = {
        numbers: [ 
            { id: 9, value: 9 },
            { id: 8, value: 8 },
            { id: 7, value: 7 },
            { id: 6, value: 6 },
            { id: 5, value: 5 },
            { id: 4, value: 4 },
            { id: 3, value: 3 },
            { id: 2, value: 2 },
            { id: 1, value: 1 },
            { id: 0, value: 0 },
            { id: -1, value: "." },
        ],

        enteredNums: this.props.value
    };

       
       

    render() {
        return (
            <div className="number_box">
                
                {this.state.numbers.map(n => <button className="nums" key={n.id} 
                        onClick={(event) => this.props.onClick(event.target.innerHTML)
                } >{n.value}</button>)}


            </div>
        );
    };
};

export default Number;