import React, { Component } from "react";




class Symbol extends Component{
    state = {
        keys: [ 
            { id: 0, value: "AC" },
            { id: 1, value: "%" },
            { id: 3, value: "÷" },
            { id: 4, value: "-" },
            { id: 6, value: "=" },
            { id: 5, value: "+" },
            { id: 7, value: "x" },
            { id: 8, value: "-/+" }
        ],


        total: 0

    };



    render(){
        return(
            <div className="symbol">
                {this.state.keys.map((n) => <button className="btn-symbol" key={n.id} 
                 onClick={(event) => {
                     this.props.onClick(event.target.innerHTML)
                     }
                }>{n.value}</button>)}
            </div>
        );
    } 

}


export default Symbol;